
import { defineComponent } from "vue";
import {
  NDivider,
  NGrid,
  NGridItem,
  NAvatar,
  NA,
  NCard,
  NText,
  NSpace,
  NH2,
} from "naive-ui";
// @ts-ignore
import { getDeviations } from "./../util.js";

export default defineComponent({
  components: {
    NDivider,
    NGrid,
    NGridItem,
    NAvatar,
    NA,
    NCard,
    NText,
    NSpace,
    NH2,
  },
  data() {
    return { count: 1, results: [], avatarUrl: "", name: "" };
  },
  async created() {
    const result = await getDeviations(
      "https://backend.deviantart.com/rss.xml?q=gallery:ssjrzamasugod"
    );
    if (result) {
      this.avatarUrl = result[0].deviantAvatar;
      this.name = result[0].deviantName;
    }
    this.results = result;
  },
  methods: {
    increment() {
      this.count++;
    },
    decrement() {
      this.count--;
    },
  },
});
